import qs from 'qs'
export default {
	data() {
		return {
			result: null,
			tid_str: null,
			personality:{
				0:[],
				1:[],
				2:[],
				3:[],
				4:[]
			}
		};
	},
	onLoad() {
		uni.getStorage({
			key: 'wm_list',
			success: (res) => {
				this.result = res.data
				for (let i = 0; i < this.result.net_list.length; i++) {
					this.getTransifrom(this.result.net_list[i].net_name,i)
				}

			}
		})
		this.tid_str = uni.getStorageSync('tid_text')
		uni.request({
			url: this.$store.state.baseUrl+'/api/netname/view',
			data: {
				ntoken: uni.getStorageSync('token'),
				app: 1
			},
			header: {
				"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
			},
			method:'POST',
			success:(res)=>{
				if(res.data.code==1){
					uni.setStorageSync('pay_view',res.data.data.order_config)
				}
			}
		})
		
	},
	methods: {
		submit_pay(){
			uni.navigateTo({
				url: '../pay/pay'
			})
		},
		toDetail(id,tk){
			let obj={
				base_id:id,
				tk:tk
			}
			uni.navigateTo({
				url:'../webview/webview?'+qs.stringify(obj)
			})
		},
		copyName(name) {
			uni.setClipboardData({
				data: name,
				success: function() {
					uni.showToast({
						title: '复制成功',
						icon: 'none'
					})
				}
			});
		},
		getTransifrom(name,i,o) {
			let obj = {
				net_name: name,
				app: 1,
				not_change_words: name.slice(1,2),
				dc: 0,
			}
			if(o){
				obj['lc[]']=o
			}
			uni.request({
				url: 'https://qiming.yw11.com/wangming/index/change_net',
				method: 'GET',
				data: obj,
				header: {
					"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
				},
				success: (res) => {
					if(res.data.code==1){
						this.personality[i]=res.data.data
					}
				}
			})
		}
	}
}
